.app {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
  background-color: #212121;
  font-family: 'Arial', sans-serif;
  color: #fafafa;
  transition: all 0.3s ease;
}

body {
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

.title {
  margin-bottom: 50px;
  color: #88d8d8;
  font-size: 2.5em;
  position: relative;
}

.title:after {
    content: "";
    position: absolute;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);
    height: 3px;
    width: 0;
    background: #88d8d8;
    transition: width 0.3s;
}

.title:hover:after {
    width: 100%;
}

.phrase {
  margin-bottom: 20px;
  font-size: 1.5em;
  color: #88d8d8;
}

.form {
  display: flex;
  justify-content: center;
  margin-bottom: 20px;
}

.input {
  width: 300px;
  height: 40px;
  padding: 10px;
  border: none;
  border-radius: 5px;
  margin-right: 20px;
  font-size: 1.2em;
  background-color: #303030;
  color: #fafafa;
}

.submit-btn, .next-btn {
  border: none;
  background-color: #88d8d8;
  color: #212121;
  padding: 10px 20px;
  font-size: 1.2em;
  border-radius: 5px;
  transition: background-color 0.2s ease, box-shadow 0.2s ease, transform 0.2s ease;
}

.submit-btn:hover, .next-btn:hover {
  background-color: #66b2b2;
  cursor: pointer;
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.1);
  transform: scale(1.05);
}

.feedback {
  font-size: 1.2em;
  margin-bottom: 20px;
}

.correct {
  color: #88c888;
}

.incorrect {
  color: #d88888;
}

@media (max-width: 600px) {
  .title {
    font-size: 2em;
    margin-bottom: 30px;
  }

  .phrase {
    font-size: 1.2em;
    margin-bottom: 15px;
  }

  .form {
    flex-direction: column;
    align-items: center;
  }

  .input {
    width: 80%;
    margin-bottom: 20px;
    margin-right: 0;
   .submit-btn, .next-btn {
    width: 80%;
  }

  .feedback {
    font-size: 1em;
    margin-bottom: 10px;
  }
}
}

